<template>
  <div class="home">
    <div class="section-wrapper">
      <div class="table-controller">
        <div class="controller-list">
          <div class="controller-item">
            <span class="controller-label">项目ID：</span>
            <Input v-model="filters.code" @on-change="onInputParams" placeholder="项目编码" class="controller-input"/>
          </div>
          <div class="controller-item">
            <span class="controller-label">版本号：</span>
            <Input v-model="filters.version" @on-change="onInputParams" placeholder="版本号" class="controller-input"/>
          </div>
          <div class="controller-item">
            <span class="controller-label">状态：</span>
            <Select v-model="filters.status"  class="controller-input" @on-change="getData">
              <Option value="all">全部</Option>
              <Option :value="1">测试</Option>
              <Option :value="2">正式</Option>
            </Select>
          </div>
        </div>
        <div class="btn-group">
          <Button type="primary" @click="resourceModal=true">上传资源</Button>
        </div>
      </div>
      <Table style="margin: 15px 0"  :loading='tableLoading' :columns="columns" :data="data" >
        <template slot-scope="{ row }" slot="action" >
          <Button type="error" @click="_deleteResources(row)">删除</Button>
        </template>
      </Table>
      <div class="page">
         <Page
         :total="total"
         show-sizer
         show-total
         show-elevator
         @on-change='changePage'
         @on-page-size-change='changePageSize'
         :current="page"
         :page-size='pageSize'/>
      </div>
    </div>
    <Modal @on-visible-change="closeModal" title='上传资源' v-model="resourceModal">
      <Form ref="form" :model="form" :rules="ruleValidate" :label-width="80">
        <FormItem label="项目ID" prop="code">
            <Select v-model="form.code" @on-change="changeCode">
              <Option v-for="item in projectList" :key="item.id" :value="item.code">{{item.code}}</Option>
            </Select>
        </FormItem>
        <FormItem label="项目名称" prop="name">
            <Select v-model="form.name" @on-change="changeName">
              <Option v-for="item in projectList" :key="item.id" :value="item.code">{{item.name}}</Option>
            </Select>
        </FormItem>
        <FormItem label="状态" prop="status">
            <RadioGroup v-model="form.status">
              <Radio :label="1">测试</Radio>
              <Radio :label="2">正式</Radio>
            </RadioGroup>
        </FormItem>
        <FormItem label="上传资源" prop="fileName">
          <Upload action="." :before-upload="onFileSelected" type="drag">
            <div style="padding: 20px 0" v-if="!form.fileName">
              <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
              <p>拖动或者点击上传资源包</p>
            </div>
            <div v-if="form.fileName">
              {{form.fileName}}
            </div>
          </Upload>
        </FormItem>
      </Form>
      <template slot="footer">
        <Button @click="resourceModal=false">取消</Button>
        <Button type="success" @click="save" :loading="loading">确定</Button>
      </template>
    </Modal>
  </div>
</template>

<script>
import { debounce } from '@/common/js/util';
import {
  getUnityList,
  deleteUnity,
  uploadResource,
} from '@/api/unity';
import { getProjectAllList } from '@/api/project';

const STATUS_MAP = {
  1: '测试',
  2: '正式',
};
export default {
  data() {
    return {
      projectList: [],
      codeNameMap: {},
      tableLoading: false,
      data: [],
      columns: [
        {
          title: '项目ID',
          key: 'code',
          align: 'center',
        },
        // {
        //   title: '项目名称',
        //   key: 'name',
        //   align: 'center',
        // },
        {
          title: '版本号',
          key: 'version',
          align: 'center',
        },
        {
          title: '测试服路径',
          key: 'localPath',
          align: 'center',
          render: (h, params) => h('a', { attrs: { href: params.row.localPath } }, params.row.localPath),
        },
        {
          title: 'cdn路径',
          key: 'cdnPath',
          align: 'center',
          render: (h, params) => h('a', { attrs: { href: params.row.cdnPath } }, params.row.cdnPath),
        },
        {
          title: '状态',
          key: 'status',
          align: 'center',
          render: (h, params) => h('span', STATUS_MAP[params.row.status]),
        },
        {
          title: '时间',
          key: 'createTime',
          align: 'center',
        },
        {
          title: '操作',
          slot: 'action',
          align: 'center',
        },
      ],
      filters: {
        code: '',
        status: 'all',
        version: '',
      },
      page: 1,
      pageSize: 10,
      total: 1,
      resourceModal: false,
      loading: false,
      form: {
        code: '',
        name: '',
        status: 1,
        fileName: '',
      },
      ruleValidate: {
        code: [
          { required: true, message: '请选择项目ID', trigger: 'change' },
        ],
        name: [
          { required: true, message: '请选择项目名称', trigger: 'change' },
        ],
        status: [
          { required: true, message: '请选择状态' },
        ],
        fileName: [
          { required: true, message: '请选择上传资源' },
        ],
      },
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.getData();
      this._getProjectList();
    },
    _getProjectList() {
      getProjectAllList()
        .then((res) => {
          this.projectList = res;
          this.projectList.forEach((item) => {
            this.codeNameMap[item.code] = item.name;
          });
        })
        .catch(() => {
        });
    },
    getData() {
      const params = {
        code: this.filters.code,
        status: this.filters.status === 'all' ? '' : this.filters.status,
        version: this.filters.version,
        page: this.page,
        size: this.pageSize,
      };
      this.tableLoading = true;
      getUnityList(params)
        .then((res) => {
          const { list, total } = res;
          this.data = list;
          this.total = total;
          this.tableLoading = false;
        })
        .catch(() => {
          this.tableLoading = false;
        });
    },
    onInputParams: debounce(function () {
      this.getData();
    }, 600),
    _deleteResources(row) {
      this.$Modal.confirm({
        title: '删除资源',
        content: '<p>确认删除此资源？</p>',
        onOk: () => {
          deleteUnity(row.id)
            .then(() => {
              this.$Message.success('删除成功');
              this.getData();
            })
            .catch(() => {
            });
        },
      });
    },
    changePage(val) {
      this.page = val;
      this.getData();
    },
    changePageSize(val) {
      this.pageSize = val;
      this.getData();
    },
    closeModal(isShow) {
      if (isShow) {
        this.$refs.form.resetFields();
      }
    },
    onFileSelected(file) {
      this.form.zipFile = file;
      this.form.fileName = file.name;
      return false;
    },
    save() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          const params = {
            code: this.form.code,
            name: this.codeNameMap[this.form.name],
            zipFile: this.form.zipFile,
            status: this.form.status,
          };
          this.loading = true;
          uploadResource(params)
            .then(() => {
              this.$Message.success('上传成功');
              this.getData();
              this.loading = false;
              this.resourceModal = false;
            })
            .catch(() => {
              this.loading = false;
              this.resourceModal = false;
            });
        }
      });
    },
    changeCode(val) {
      this.form.name = val;
    },
    changeName(val) {
      this.form.code = val;
    },
  },
};
</script>

<style lang="less" scoped>
  .home{
    .page{
      display: flex;
      justify-content: flex-end;
    }
  }
</style>
